a{
  color:black;
  text-decoration: none;
}

a::after{
  text-decoration: none;
}

a:active{
  color:inherit;
  text-decoration: none;
}

.navbar-element{
  margin-top: 0.5vh;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  width:60px;
  padding:5px;
  cursor: pointer;
  text-align: center;
}

.navbar-element:hover{
  font-size: 16px;
  font-weight: bold;
  text-decoration:double;
  transition-duration: 0.5s;
}

@media screen and (min-width:767px){

.contact{
  position:fixed;
  top:40vh;
  left:0px;
  padding:5px;
  width:30px;
  border: 1px solid lightgray;
  border-left: none;
}

.contact-icon{
  color:gray;
}

.contact-icon:hover{
  color:black;
}

.display{
  border-radius:50%;
  height:35%;
  margin-top:30vh;
  margin-left:20vw;
}

.filler{
  height:20%;
  margin-left:75vw;
  margin-top:10vh;
}

.name-header{
  font-size: 2vw;
  font-family: Arial, Helvetica, sans-serif;
}

.name-sub-header{
  font-size: 1vw;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 10px;
}

.navbar{
  display: flex;
  justify-content: flex-end;
  width:100%;
  height:5vh;
  position:fixed;
  border-bottom: 1px solid lightgray;
  backdrop-filter: blur(10px);
}



.title {
  margin-top:-9vh;
  margin-right:25vw;
  text-align: right;
}

.view-one{
  height: 100vh;
  background-color: white;
}

.view-two{
  background-color: lightblue;
}

}

@media screen and (max-width:767px){

  .contact{
    position:fixed;
    top:40vh;
    left:0px;
    padding:5px;
    width:30px;
    border: 1px solid lightgray;
    border-left: none;
  }
  
  .contact-icon{
    color:gray;
  }
  
  .contact-icon:hover{
    color:black;
  }
  
  .display{
    border-radius:50%;
    height:20%;
    margin-top:20vh;
    margin-left:20vw;
  }
  
  .filler{
    height:20%;
    margin-left:60vw;
    margin-top:10vh;
  }
  
  .name-header{
    font-size: 3vh;
    font-family: Arial, Helvetica, sans-serif;
    width:70%;
  }
  
  .name-sub-header{
    font-size: 2vh;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 10px;
    width:70%;
  }
  
  .navbar{
    display: flex;
    justify-content: flex-end;
    width:100%;
    height:5vh;
    position:fixed;
    border-bottom: 1px solid lightgray;
    backdrop-filter: blur(10px);
  }
  
  
  .title {
    margin-top:-9vh;
    margin-right: -15vh;
    text-align: right;
    padding-top:20%;
  }
  
  .view-one{
    height: 100vh;
    background-color: white;
  }
  
  .view-two{
    background-color: lightblue;
  }
  
  }












